import React from "react";
import "./Summary.css"

const Summary = props =>
    <div id="imagesMain">
                <img src={'https://raw.githubusercontent.com/EquipRENTAL/Homepage/master/assets/img/gear/campingIcons.jpg'} alt="lets go" />
                <img src={'https://raw.githubusercontent.com/EquipRENTAL/Homepage/master/assets/img/parallax/campIcons.png'} alt="choices" />
                <img src={'https://thumbs.dreamstime.com/b/couple-backpackers-vacation-traveling-47917394.jpg'} alt="campers" />
    </div>

export default Summary;


